body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Home{
  width: 100%;
  position: relative;
  text-align: center;
  position: absolute;
  font-family: arial, sans-serif;
}

.header{
  position: absolute;
  width: 100%;
  height: 8vw;
  z-index: 1;
  top: 0;
  padding: 0;
  color: #fcfff5;
  line-height: 1.5;
  background-color: rgba(0,0,0, 0.7);
}

.logo{
  max-height: 90%;
  width: auto;
  position: absolute;
  left: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  margin-left: 2%;
}

.title{
  position: absolute;
  width: 80%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-45%, -50%);
          transform: translate(-45%, -50%);
  margin: 0;
  font-size: 4vw;
  font-weight: bold;
  font-family: 'Righteous', cursive;
}


.gallery{
  position: relative;
  top: 10vw;
  left: 20%;
  width: 75%;
  padding: 1em 0.5em;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: center;
          align-content: center;
  border: 1px solid rgba(0, 0, 0, 0.3);
  background-color: rgba(0, 0, 0, 0.1);
}


.item-wrapper{
  position: relative;
  width: 22%;
  min-height: 30vm;
  margin: 0.5em 0.3em;
  display: inline-block;
  background-color: rgba(0, 0, 0, 0.8);
  box-shadow: 0 0.2em 0.5em 0 rgba(0,0,0,0.8);
}

.gallery-item{
  opacity: 0.8;
  cursor: pointer;
  text-decoration: none;
  color: #F2F2F2;
}

.gallery-item:hover{
  opacity: 1;
}

.tag{
  overflow: auto;
  position: relative;
  margin: 0.5vw 0;
}

.movie-name{
  position: relative;
  display: block;
  margin: 0;
  font-size: 1.5vw;
  font-weight: bold;
  color:#d6f8b8;
}

.movie-rating{
  position: relative;
  display: block;
  margin: 0.2vw 0;
  font-size: 1vw;
  color: #8fbbaf;
}

.poster{
  width: 100%;
  height: auto;
	max-height: 80%;
}


.filter-list{
  position: absolute;
  top: 10vw;
  left: 2%;
  width: 15%;
  border: 1px solid rgba(0, 0, 0, 0.3);
}

.genre-list{
  text-align: left;
  position: relative;
  font-size: 1vw;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}

.genre-list h3{
  padding: 0.5em 0.3em;
  margin: 0;
  font-size: 2vw;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}

.genre{
  display: block;
  overflow: hidden;
  width: 100%;
  height: 2.5em;
  border: none;
  cursor: pointer;
}

.genre-name{
  margin: 0 0.5em;
  padding: 0;
  text-align: left;
  font-size: 1.5em;
  line-height: 1.5em;
}

.genre:hover{
  background-color: rgba(0, 0, 0, 0.05);
}

.active{
  background-color: rgba(0, 0, 0, 0.05);
  font-weight: bold;
}

.Details{
  width: 100%;
  position: relative;
  text-align: center;
  position: absolute;
  font-family: arial, sans-serif;
}

.detail-container{
  position: relative;
  top: 10em;
  width: 70%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  padding: 2em 0.5em;
  /* display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-content: center; */
  text-align: left;
  border: 1px solid rgba(0, 0, 0, 0.3);
  background-color: #e3e7f1;
  border-radius: 1em;
}

.arrow-left{
  position: absolute;
  width: 2em;
  height: 2em;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  cursor: pointer;
  opacity: 0.6;
}

.arrow-right{
  position: absolute;
  width: 2em;
  height: 2em;
  right: 0.5em;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  cursor: pointer;
  opacity: 0.6;
}

.arrow-left:hover, .arrow-right:hover{
  opacity: 1;
}

.detail-poster{
  position: relative;
  left: 6%;
  width: 30%;
  height: auto;
  max-height: 80%;
  border-radius: 0.8em 2em;
}

.detail-tag{
  position: absolute;
  overflow: auto;
  padding: 1vw;
  left: 40%;
  height: 80%;
  width: 50%;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  font-size: 1.2em;
}

.detail-ab{
  position: relative;
  font-weight: bold;
  color: #113f67;
  font-size: 1.8vw;
  margin: 2% 0 1% 0;
}

.detail-name{
  position: relative;
  max-height: 30%;
  width: 100%;
  color: #113f67;
  text-align: center;
  font-size: 3vw;
  margin: 1vw 0;
}

.detail-date{
  position: relative;
  /* height: 2em; */
  width: 100%;
  color: #494ca2;
  margin: 0;
  font-size: 1.6vw;
}

.detail-rating{
  position: relative;
  width: 100%;
  /* height: 2em; */
  color: #8f71ff;
  margin: 0;
  font-size: 1.6vw;
}

.detail-overview{
  position: relative;
  text-align: justify;
  /* top: 2%; */
  width: 100%;
  /* height: 50%; */
  color: #494ca2;
  margin: 0;
  font-size: 1.5vw;
}

.List{
  width: 100%;
  position: relative;
  text-align: center;
  position: absolute;
  font-family: arial, sans-serif;
}

.search-container{
  position: absolute;
  top: 9vw;
  width: 100%;
  height: 3vw;
  text-align: left;
}

.search-bar{
  position: absolute;
  width: 70%;
  height: 100%;
  left: 5%;
  border-radius: 18px;
  border: 1px solid rgba(0, 0, 0, 0.3);
}

.search-icon{
  position: absolute;
  width: 2vw;
  height: 2vw;
  color: rgba(0,0,0, 0.3);
  left: 0.3vw;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.search-icon:hover{
  color: rgba(0,0,0, 0.7);
}

.search-input{
  position: absolute;
  height: 100%;
  width: 90%;
  border: none;
  left: 6%;
  font-size: 1.6vw;
  padding: 0;
  border-radius: 18px;
  line-height: 1.6vw;
}

.search-input:focus{
  outline: none;
}

.search-submit {
  display: inline-block;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  left: 78%;
  background-color: #dfe2e2;
  padding: 0;
  width: 8vw;
  height: 3vw;
  font-size: 1.5vw;
  line-height: 1.2vw;
  border-radius: 1vw;
  border: none;
  cursor: pointer;
  font-weight: bold;
  color: #3c4f65;
}

.search-submit:hover {
  font-weight: bold;
  border: 1px solid rgba(0, 0, 0, 0.3);
  color: black;
}

.search-submit:focus {
  outline:0;
}


.toggle-container{
  height: 2.5vw;
  min-width: 6vw;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row, wrap;
          flex-flow: row, wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  position: absolute;
  right: 0;
  bottom: 0;
  line-height: 2.5vw;
}

.toggle-button{
  height: 2.5vw;
  width: 3vw;
  cursor: pointer;
  background-color: (0,0,0,0.7);
  padding: 0;
  border-radius: 0.2vw;
  border: none;
  padding: 0.1vw;
  opacity: 0.7;
}

.toggle-button:hover{
  opacity: 0.9;
}

.toggle-icon{
  height: 100%;
  width: 100%;
}

.toggle-active{
  color: white;
  background-color: black;
  opacity: 1;
}

.sort-container-1{
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  top: 13vw;
  width: 30%;
  height: 2.5vw;
  left: 5%;
  line-height: 1.5vw;
}

.sort-word{
  font-size: 2vw;
  line-height: 2vw;
  padding: 0;
  margin: 0 0.5vw;
  color: #3c4f65;
}

.total_res{
  position: absolute;
  width: auto;
  top: 12vw;
  left: 65%;
  height: 2vw;
  line-height: 2vw;
  font-size: 1.2vw;
  font-style: italic;
  color: rgba(200, 0, 0, 0.5);
}

.sort-form{
  width: 20%;
  padding: 0;
  margin: 0;
  line-height: 2.5vw;
}

.sort-form select{
  font-size: 1.5vw;
  top: 50%;
  color: grey;
}

.sort-container-2{
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  top: 16vw;
  width: 30%;
  height: 2.5vw;
  left: 5%;
  line-height: 1.5vw;
}


.list{
  position: relative;
  top: 20vw;
  width: 90%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  padding: 1vw 0.5vw;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  border: 1px solid rgba(0, 0, 0, 0.3);
  background-color: rgba(0, 0, 0, 0.1);
}

.list-item{
  position: relative;
  width: 90%;
  height: auto;
  min-height: 20vw;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  margin: 0.5vw 0.3vw;
  background-color: #dfe2e2;
  box-shadow: 0 0.2vw 0.5vw 0 rgba(0,0,0,0.4);
  color: #F2F2F2;
  text-align: left;
}

.list-poster{
  width: 20%;
  height: auto;
  max-height: 20em;
}

.list-tag{
  display: inline-block;
  position: absolute;
  overflow: auto;
  left: 25%;
  height: 80%;
  width: 70%;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.list-movie-name{
  color: #113f67;
  font-size: 2vw;
  margin: 1vw 0;
}

.list-movie-rating, .list-movie-date, .list-movie-overview{
  color: #34699a;
  padding: 0;
  font-size: 1.5vw;
  margin: 0.5vw;
}

